        blogAuthor: "Subabrata Nath",
        blogAuthorImg: subabrata,
        blogTitle: 'Top 8 Vision myths busted!',
        blogPreview: `There are many myths that we have been told from ages, like chocolate lacks health benefits(yes, its a myth, but dont overeat though) and many more like that, all these are nothing but fiction ,well at least the science says so. And our eyes being one of the most neglected organ, there are a lot of myths that surrounds our eyes. Well, I think its time to get to the bottom of all these myths and get some vision facts once and for all because you cant take care of your eyesight properly if you get caught up in these myths.`,
        blogText: `</p>There are many myths that we have been told from ages, like chocolate lacks health benefits(yes, its a myth, but dont overeat though) and many more like that, all these are nothing but fiction ,well at least the science says so. And our eyes being one of the most neglected organ, there are a lot of myths that surrounds our eyes. Well, I think its time to get to the bottom of all these myths and get some vision facts once and for all because you cant take care of your eyesight properly if you get caught up in these myths.</p>
        <p>Below weve listed some of the top myths about your eyes, with some facts.</p>
        <ol>
        <li>
            <h3>Eating carrots improves eyesight.</h3>
            <h4>False</h4>
            <p>Vitamin A (or retinol) is an essential nutrient to maintain healthy sight. However, the requirement to the body is relatively low and can be easily obtained from green leafy vegetables, dairy and fishes. Even though Carrots are known to be a good source of beta carotene which gets converted into vitamin A, there is no scientific evidence to prove the above statement. So no, eating a dozen carrots wont give you night vision. </p>
        </li>
        <li>
            <h3>Sitting near TV harms your eyes.</h3>
            <h4>False</h4>
            <p>We were scolded countless times for sitting too close to the TV, as this supposedly harm our eyes, especially when we were kids, but what most elders do not know is that children have a better near vision thats why they prefer sitting close to the television, VR makes so much sense now. There are but only short term effects such as headache or discomfort. No long term adverse effects have been found yet.</p>
        </li>
        <li>
            <h3>Reading in dim lights could be harmful.</h3>
            <h4>False</h4>
            <p>Our eyes are cleverly designed to adjust themselves according to the light available. When we are in low light our pupil dilates and allows more amount of light to fall into the lens and vice versa. So reading in low light for a long time will only strain your eyes, though you would be more comfortable reading under a lamp.</p>
        </li>
        <li>
            <h3>Glasses make your eyes weaker.</h3>
            <h4>False</h4>
            <p>Glasses only helps an individual to see their best. They neither make your eyes weaker nor correct your eyesight, or they do so only when you are wearing them. Our eyesight changes naturally and as we got older the eye lens gets thicker and so it can not change focus from distance to near. So our eyes get weaker due to ageing and not because of glasses. </p>
        </li>
        <li>
            <h3>No eye exam needed until you have a problem.</h3>
            <h4>False</h4>
            <p>Having an eye exam on a yearly basis is as important as to have a regular health check-up. As the saying goes prevention is better than cure, it equally validates when it comes to your eyes. Doing a yearly eye exam helps you maintain eye health and in earlier detection of diseases, such as high blood pressure, brain tumour and even diabetes which could cause vision loss if untreated. Now understand that an eye exam is different from an eye test/sight test, which is basically performed to prescribe an accurate eyeglass. </p>
        </li>
        <li>
            <h3>Eye exercise for better vision</h3>
            <h4>False</h4>
            <p>Contrary to popular belief eye exercises can not help in improving your eyesight, however, eye exercise might come handy in relaxing the eye muscles and treating eye muscles problem such as misalignment, known as strabismus, which is just another fancy name for a crossed eye condition. No amount of eye gymnastics can cure near or farsightedness.</p>
        </li>
        <li>
            <h3>You will outgrow crossed eye.</h3>
            <h4>False</h4>
            <p>Weve been told not to cross our eyes or we would get crossed eyes, however, crossed eyes are results of nerve damage and can not be caused due to prolonged crossed eye position. Which means you can keep on making that funny face without worrying, but do stop when it starts to hurt.</p>
        </li>
        <li>
            <h3>You see from your eyes</h3>
            <h4>False</h4>
            <p>Well technically speaking its your brain that process the electrical signal from the optic nerve and convert it into what you see. Your eyes are just photo (light) receptors that catch the reflected lights from the objects, convert them into an electrical signal (via retina) and send it to your brain via the optic nerve. Your brain can process an image that eye sees for even 13 milliseconds.</p>
        </li><br />
        <p>These are but not all the myths that surrounds our eyes. Do your research and educate yourself. So next time somebody tells something which is not true you would have an awesome science backed comeback .And remember have faith in science and not in fiction, fiction only looks great in books and movies like Harry Potter (Accio readers, ugh nevermind).</p>
            `,
