        blogAuthor: "Antra Sharma",
        blogAuthorImg: antra,
        blogTitle: 'Old Eyes, New Care',
        blogPreview: `It is the old pair of eyes that have seen the most. Old people are the vision to the future and to protect this vision from falling a victim to the storm of eye related diseases, it is essential that older people have good access to eye-care and eye-care related services.
                    It is of no news that science and technology go hand-in-hand and have proved to cure many diseases and at the same time have also eased human lives big time. But back in the old days people did not have access to the technology advanced and scientifically approved treatments of the eye diseases. Lack of awareness about eye-care, diseases and their symptoms also contributed to problems in vision and in extreme cases vision loss in old age people`,
        blogText: `<p>It is the old pair of eyes that have seen the most. Old people are the vision to the future and to protect this vision from falling a victim to the storm of eye related diseases, it is essential that older people have good access to eye-care and eye-care related services.</p>
                    <p>It is of no news that science and technology go hand-in-hand and have proved to cure many diseases and at the same time have also eased human lives big time. But back in the old days people did not have access to the technology advanced and scientifically approved treatments of the eye diseases. Lack of awareness about eye-care, diseases and their symptoms also contributed to problems in vision and in extreme cases vision loss in old age people.</p>
                    <p>But with advancement in technology and new discoveries in science, diagnosing diseases at early stages have become easy and have led to success of health programs by many folds, also supporting the fact that life expectancy rate have also increased. Though difficulties relating to the eyes and vision can occur at any age but for most eye diseases like cataract, glaucoma and Age-Related Macular Degeneration (AMD) are found to be more common in the senior citizens. Where comorbidities like diabetes, high blood pressure and cardiovascular disease increase the risk of age-related eye diseases. Old age doesnt have to be dotage. All it requires is some care for your eyes so, that you can see the magic from your own eyes. (That too without any pixie dust!).</p>
                    <p>Eye-care can be aced by following some tips that can be a gateway to healthy eyes.</p>
                    <h3>Regular eye exams</h3>
                    <p>The importance of regular eye-check up with an ophthalmologist cannot be overlooked in this age group. As old age people are at more risk in developing diseases like cataract, AMD, glaucoma etc. Insights about comorbidities like diabetes and hypertension are also provided when ophthalmologist take a glance at the eyes.</p>
                    <h3>Healthy lifestyle leads to Normal blood pressure, cholesterol and glucose levels</h3>
                    <p>Eating a diet rich in vitamin A & C (leafy greens, fruits, nuts) will keep your blood pressure, cholesterol and glucose levels in control. Systemic diseases such as hypertension, diabetes, and atherosclerosis are not only linked to cardiovascular disorders. But they also cause the risk of vision loss due to adverse changes in the blood vessels of the eyes. These can range from haemorrhages, exudations, and blockage of blood vessels in the eye.</p>
                    <h3>Maintain a healthy weight</h3>
                    <p>It is important that a healthy weight is maintained as being overweight increases your risk of diabetes, which can lead to diabetic retinopathy. It is advised to consume a balanced diet and constitute exercises in ones routine will help in making the mind and body healthy. By exercising regularly blood circulation is ensured and there is adequate oxygen intake. </p>
                    <h3>Wear protective glasses</h3>
                    <p>Protecting eyes from the harmful ultraviolet rays from the sun is important. It should not only looked as a means of fashion but wearing a hat with a brim, or a scarf/ dupatta can shield your eyes. Wear sunglasses, right prescription glasses with a UV protecting coating.</p>
                    <h3>Use good lighting</h3>
                    <p>For your eyes to be able to function well, your eyes need 3 times as much light when you're 60 as they did when you were 20. Increase the daylight in your home by keeping windows clean and curtains pulled back.<p>
                    <p>Make sure you have good lighting be it from electricity or as sunlight, make a note you have good lighting especially at the top and bottom of stairs so you can see the steps clearly.</p>
                    <p>For reading or close work, use a direct light from a flexible table lamp, positioned so the light isn't reflected by the page causing glare or strain in the eyes.</p>
                    <h3>Exercises supporting old age eyesight:</h3>
                    <h4>Palm the eyes</h4>
                    <p>Palming the eyes helps to relax eye muscles and reduce eye tension. Ask your grandparents to gently place palm of the hands over the eyes without applying any force or pressure. This should be clubbed with taking deep breaths for a couple of minutes.</p>
                    <h4>Look in four directions</h4>
                    <p>Ask your grandparents to stand straight having face in the forward direction and move their eyes to one side and focus on any object. Then repeating this in the opposite direction. Similarly ask them to do this exercise in the upward direction as well as in the downward direction. This exercise will help strengthen optical nerves and muscles.</p>
                    <h4>Tracing</h4>
                    <p>Look at any object within your field of vision and focus on it. Trace the outline of the object in focus. Repeat this exercise 3-4 times a day.</p>
                    <h4>Tracking near and far</h4>
                    <p>Stretch your arm out and make a thumbs-up and then focus on your thumb tip and slowly bring it close to your face at about three inches to your face. Now, staying focusses reverse the action and move your thumb slowly back to its original position. </p><br />
                    <p>Old age people are just like the roots of the trees and the branches are as grandchildren. Therefore, in order to keep these branches nourished and facilitate growth it is the responsibility of the young to take care of the old people so, that they can get going from narrating bed time stories, reading newspaper to talking about their life experiences without any difficulties. </p>
                    <p>Getting them to go for regular eye check-ups and if they want to opt for eye surgeries, it should be followed by consulting the ophthalmologist and discussing with them in detail about the problems faced, past medical records and if there will be any side effects after surgeries. Some surgeries that old age people are likely to get are cataract surgery, laser surgery, trabeculectomy for glaucoma and other. Vision related problems should not be taken for granted as poor vision can result in disturbances in daily life activities, when staying active is the key to maintain health and quality of life of the senior citizens.</p>
                    <p>As far as the eye health of the seniors is concerned, it is essential to encourage healthy lifestyles that boost eye health in the long run. As their golden years and their golden eyes should never lose their shine. </p>
                    <p>We couldnt do much then but we can surely do it now. Lets be the driving force that can make a difference in their lives!</p>
                    `,
