        blogAuthor: "Nivit Karki",
        blogAuthorImg: nivit,
        blogTitle: 'Sports for Healthy vision',
        blogPreview: `Waking up late in the morning has become a part of lifestyle and a sporty morning start has somewhat demised from our lives. With this, the urge to do physical activities has been suppressed. With the dozy eyes and half-filled cup of coffee, we forget that we are slowly losing the hold of an elusive situation, which is our physical fitness.`,
        blogText: `<p>Waking up late in the morning has become a part of lifestyle and a sporty morning start has somewhat demised from our lives. With this, the urge to do physical activities has been suppressed. With the dozy eyes and half-filled cup of coffee, we forget that we are slowly losing the hold of an elusive situation, which is our physical fitness.</p>
                    <p>While the fitness gurus and trainers talk about the outer fitness, seldom do they talk about our eye fitness. Many ophthalmologists do point out the need of sports and exercise for eye care but everything goes in vain as soon as we come out of their clinic. Little did we know that there is a field of specialization in sports vision, which aims at increasing the response time of eyes while playing any sport. Just as constant exercise increase ones speed and accuracy, studies suggest that they can also improve ones visual speed and accuracy. A Fun Fact! Do you know, 80% of sensory input in sports is visual? To further strengthen the above claim, let us consider a scenario. Imagine a person wants to grow his biceps and other muscular organ for any tournament or just for fitness. He might spend few hours hitting gym every day. Now, after few months, he might get desired shape of muscle. Similarly our eyes have a network of various tissues and muscles known as ciliary muscle which can be strengthened by doing appropriate exercises. Intriguing isnt it?</p>
                    <p>Not only the betterment but some studies have found that exercise is directly related to reduced risk of glaucoma, cataract, wet-age macular degeneration and others. We hardly ever see any athlete, in his old age, wearing any prescription glasses, and lot can be attributed to the fact that they play light sport even in their late years. According to a 2016 study, lack of physical exercise may be associated with increased risk of cataract. Another study suggests that exercise and sports are equally effective in treating glaucoma. Doctors treat glaucoma by reducing high intraocular eye pressure but similar results can be achieved by playing a game which includes high vision perception.</p>
                    <p>Not only this, other ailments like diabetes and high blood pressure can also significantly damage our eyes, and can make us blind. So in that case doctors suggest exercise. So if you are someone who is suffering from these problems, then sports and exercise should be at the top of your bucket list. Diabetes can severely damage the retinal nerves hence it is highly recommended to do light exercise in this condition.</p>
                    <h4>So What Can You Do?</h4>
                    <ul>
                        <li>You should eat nutritious food and visit doctor regularly for check-ups.</li>
                        <li>You should exercise everyday as it is beneficial for body, mind as well as your eye.</li>
                    </ul>
                    <p>Compromising on any of the point can give you some gruesome result and you can be devoid of this ubiquitous yet precious resource.</p>
                    <p>Many studies have correlated exercise and better vision. So, it makes sense to stay active and play a lot of sports. We cant miss the fact that sportsperson undergoes various sports vision test, for example, alignment test, visual processing speed test, eye dominance test etc, but sports is equally important for everyone on this earth and should be played with zeal and zest for better health and vision. Your mission should be to have better vision.</p>
                    `,
        blogLink: "/blog/blogid=0007",
        buttonText: "Read full Article",
